:root {
  --whiteTextColor: #f4f4f4;
  --blackTextColor: #121212;

  --buttonColor: RoyalBlue;

  --whiteBackground: #f4f4f4;
  --greyBackground: #eceaea;

  --borderColor: #c1c1c1;

  --font: "Roboto Slab", serif;
}

html,
body {
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
}

/* Main */

.main-container {
  max-width: 1600px;
  margin: 0 auto;
}

/* Header */

.background-image {
  width: 100%;
  background-image: url(images/header.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

h1,
h2,
p {
  color: var(--whiteTextColor);
  font-family: var(--font);
}

h1 {
  font-weight: 800;
  font-size: 55px;
}

h2 {
  font-weight: 600;
  font-size: 35px;
  text-transform: uppercase;
  margin-bottom: 23px;
}

h3 {
  color: var(--blackTextColor);
  font-family: var(--font);
  font-weight: 600;
  font-size: 28px;
  text-transform: uppercase;
  margin-top: 37px;
}

#skills-h3 {
  margin-bottom: 37px;
}

p {
  font-weight: 400;
  font-size: 23px;
}

#header-p {
  margin-top: 0;
  width: 80%;
  line-height: 30px;
}

.greeting-wrapper {
  display: grid;
  text-align: center;
  align-content: center;
}

.intro-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "left-column right-column";
}

.left-column {
  grid-area: left-column;
  display: flex;
  justify-content: center;
}

.right-column {
  grid-area: right-column;
  display: grid;
  align-content: center;
  padding: 10px;
}

/* About */

.about-wrapper {
  background-color: var(--whiteBackground);
  display: grid;
  grid-template-columns: repeat(auto-fit, 524px);
  justify-content: space-evenly;
}

.about-p {
  color: var(--blackTextColor);
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
  margin-bottom: 37px;
}

#terminal {
  background-image: url(images/subhero.png);
  background-size: 100% auto;
  background-repeat: no-repeat;
}

ul {
  padding-right: 10px;
}

li {
  font-family: var(--font);
  color: var(--whiteTextColor);
  font-size: 20px;
  font-weight: 600;
  line-height: 32px;
}

#skills-wrapper {
  display: flex;
  justify-content: space-around;
  padding-top: 20px;
}

/* Projects */

#projects-header {
  background-color: var(--greyBackground);
  padding: 37px;
  text-align: center;
  margin: 0;
}

h4 {
  font-family: var(--font);
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  text-transform: uppercase;
  margin: 0;
}

.projects-wrapper {
  background-color: var(--greyBackground);
  gap: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 50px;
}

.project {
  max-width: 387px;
  border: 1px solid var(--borderColor);
  -webkit-box-shadow: -2px 7px 21px -9px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -2px 7px 21px -9px rgba(0, 0, 0, 0.75);
  box-shadow: -2px 7px 21px -9px rgba(0, 0, 0, 0.75);
}

.thumbnail {
  display: block;
  width: 387px;
  height: 220px;
  object-fit: cover;
}

.project-preview {
  background-color: var(--whiteBackground);
  padding: 15px;
  min-height: 200px;
}

.project-intro {
  font-size: 16px;
  margin-top: 8px;
  line-height: 28px;
}

.link-wrapper {
  display: flex;
  justify-content: space-between;
}

a {
  font-family: var(--font);
  text-decoration: none;
  text-transform: uppercase;
  color: var(--buttonColor);
  font-weight: 500;
  font-size: 16px;
}

a:hover {
  color: dodgerblue;
}

/* Footer */

.footer-wrapper {
  width: 100%;
  background-image: url(images/footer.png);
  background-size: 100% 100%;
  background-repeat: no-repeat;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "form-area .";
  justify-items: center;
}

.form-area {
  grid-area: form-area;
}

#form-title {
  margin: 0;
  padding: 37px;
  color: var(--whiteTextColor);
  text-align: center;
}

#contact-form {
  display: block;
  max-width: 500px;
  border-radius: 5px;
  padding: 0 45px 40px 40px;
}

.input-field {
  width: 100%;
  padding-top: 10px;
  padding-left: 20px;
  padding-bottom: 10px;
  background-color: var(--whiteBackground);
  border-radius: 5px;
  margin-bottom: 15px;
  font-size: 18px;
  border: none;
}

::placeholder {
  font-family: var(--font);
  opacity: 0.6;
}

textarea {
  height: 130px;
}

#submit-btn {
  display: block;
  margin: auto;
  width: 120px;
  height: 40px;
  background-color: var(--buttonColor);
  color: var(--whiteTextColor);
  font-family: var(--font);
  font-size: 18px;
  font-weight: 600;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  text-transform: uppercase;
}

.email-message {
  margin: 0;
  padding-top: 10px;
  padding-bottom: 0;
  text-align: center;
}

@media screen and (max-width: 1200px) {
  .footer-wrapper {
    width: 100%;
    background-image: none;
    background-color: #222;
    background-repeat: no-repeat;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: "form-area";
    justify-items: center;
  }

  #contact-form {
    padding-bottom: 0px;
    margin-bottom: 37px;
  }
}

@media screen and (max-width: 1047px) {
  .about-wrapper {
    padding-bottom: 40px;
  }

  .about-p {
    margin-bottom: 0;
  }

  .project-intro {
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 768px) {
  .intro-wrapper {
    grid-template-columns: 1fr;
    grid-template-areas:
      "left-column"
      "right-column";
  }

  .right-column {
    justify-items: center;
  }

  .right-column p {
    text-align: center;
  }

  h1 {
    font-size: 40px;
  }
  h2,
  h3 {
    font-size: 25px;
  }
  p,
  .about-p,
  li {
    font-size: 18px;
  }

  #profile-pic {
    display: block;
    margin: 0 auto;

    height: 200px;
    width: 200px;
    object-fit: cover;
  }

  .about-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas:
      "more-column"
      "expertise-column";
    padding: 0 40px 40px 40px;
  }

  #terminal {
    background-size: 100% auto;
  }
}

@media screen and (max-width: 480px) {
  h1 {
    font-size: 28px;
  }
  h2 {
    font-size: 20px;
  }
  p {
    font-size: 15px;
  }
  li {
    font-size: 15px;
  }
  .about-p {
    font-size: 15px;
  }
  h3 {
    font-size: 20px;
  }
  h4 {
    font-size: 17px;
  }

  #skills-wrapper {
    padding-top: 0;
  }

  .project {
    max-width: 300px;
    width: 100%;
    height: auto;
  }

  .thumbnail {
    display: block;
    width: 300px;
    height: 180px;
    object-fit: cover;
  }

  .input-field {
    font-size: 15px;
  }
}
